import Place from './Place';
import React, { useState, useEffect } from "react";
import { Button, Form, Container } from 'react-bootstrap'
import places from '../data';
import Listing from './Listing';

function Home() {
    const [addPlaceVisible, setPlaceVisible] = useState(false);
    const [searchTerm, setSearchTerm] = useState("");
    const [searhResult, setSearchResult] = useState([])

    function onAddPlace(name, address) {
        places.push({ name: name, address: address, reviews: [] });
    }

    function addReview(name, review) {
        let updatePos = places.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        let copy = Array.from(searhResult);
        let updatePosResult = copy.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        places[updatePos].reviews.push(review);
        copy[updatePosResult] = places[updatePos];
        setSearchResult(copy);
    }

    function onUpdatePlace(name, newName, newAddress) {
        let updatePos = places.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        let copy = Array.from(searhResult);
        let updatePosResult = copy.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        copy[updatePosResult].name = newName;
        copy[updatePosResult].address = newAddress;
        setSearchResult(copy);
        places[updatePos].name = newName;
        places[updatePos].address = newAddress;
    }

    function onDeletePlace(name) {
        let deletePos = places.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        places.splice(deletePos, 1);
        let copy = Array.from(searhResult);
        let deletePosResult = copy.findIndex(function (place) {
            if (name === place.name) {
                return true;
            }
            return false;
        })
        copy.splice(deletePosResult, 1);
        setSearchResult(copy);
    }
    function onSearch(searchText) {
        let m = []
        for (var i = 0; i < places.length; i++) {
            let current = places[i];
            let match = current.name.includes(searchText);
            if (match === true) {
                m.push(current);
            }
        }
        setSearchResult(m)
    }
    return (
        <Container fluid>
            <h1>Nearby Places</h1>
            <Button variant="primary" onClick={function () {
                setPlaceVisible(!addPlaceVisible)
            }}>Add A Place</Button>
            <Button variant="primary" onClick={function () {
                onSearch("");
            }}>Show All Places</Button>
            {addPlaceVisible === true && <Place onAddPlace={onAddPlace} />}
            <Form onSubmit={function (event) {
                event.preventDefault();
                onSearch(searchTerm);
            }}>
                <Form.Control onChange={function (event) {
                    setSearchTerm(event.target.value)
                }}
                    value={searchTerm} size="lg" type="text" placeholder="Search for a place" />
                <Button type="submit">Search</Button>
            </Form>
            {
                searhResult.map(function (place) {
                    return (
                        <Listing addReview={addReview} onUpdatePlace={onUpdatePlace} onDeletePlace={onDeletePlace} name={place.name} address={place.address} reviews={place.reviews} />
                    )
                })
            }
        </Container>
    )
}


export default Home;