import { Button, Form, Container } from 'react-bootstrap'
import React, { useState, useEffect } from "react";


function Listing(props) {
  const [updateVisible, setUpdateVisible] = useState(false);
  const [locationName, setLocationName] = useState("");
  const [address, setAddress] = useState("");
  const [ReviewVisible, setReviewVisible] = useState(false);
  const [reviewText, setReviewText] = useState("");
  return (
    <div style={{ border: '2px solid black' }}>
      <h3>{props.name}</h3>
      <h5>{props.address}</h5>
      <h4>Reviews</h4>
      {props.reviews.map(function (review) {
        return (
          <p>{review}</p>
        )
      })}
      <Button variant="primary" onClick={function () {
        setReviewVisible(!ReviewVisible);
        setUpdateVisible(false);
      }}>Add a Review</Button>
      <Button variant="primary" onClick={function () {
        setUpdateVisible(!updateVisible);
        setReviewVisible(false);
      }}>Update Place</Button>
      <Button variant="danger" onClick={function (event) {
        props.onDeletePlace(props.name);
      }} > Delete Place</Button>
      {updateVisible === true && ReviewVisible === false &&
        <Form onSubmit={function (event) {
          event.preventDefault();
          props.onUpdatePlace(props.name, locationName, address);
        }}>
          <Form.Group controlId="name">
            <Form.Label>New Name</Form.Label>
            <Form.Control value={locationName} type="text" onChange={function (event) {
              setLocationName(event.target.value)
            }}
              placeholder="Enter Name" />
            <Form.Label>New Address</Form.Label>
            <Form.Control value={address} type="text" onChange={function (event) {
              setAddress(event.target.value)
            }}
              placeholder="Address" />
            <Button type="submit">Submit Changes</Button>
          </Form.Group>

        </Form>
      }
      {ReviewVisible === true && updateVisible === false &&
        <Form onSubmit={function (event) {
          event.preventDefault();
          props.addReview(props.name, reviewText);
        }}>
          <Form.Control as="textarea" value={reviewText} rows={3} placeholder="Enter Review" onChange={function (event) {
            setReviewText(event.target.value)
          }} />
          <Button type="submit">Submit</Button>
        </Form>
      }
    </div>
  )
}
export default Listing;